.form-container {
  max-width: 500px;
  margin: 300px auto; 
  padding: 24px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: white;
}

form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

label {
  font-weight: bold;
  margin-top: 8px;
}

input, textarea {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

textarea {
  resize: vertical;
  height: 60px; 
}

.next-button {
  width: 80px;
  padding: 10px;
  background-color: #94c4e0;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 12px;
  font-weight: bold;
  font-size: 15px;
}

.next-button:hover {
  background-color: #70a4c2;
}
